var server = window.location.hostname;
var port = "1337";
var Graph = /** @class */ (function () {
    function Graph(vector, id_graph) {
        this.vector = vector;
        this.id_graph = id_graph;
    }
    return Graph;
}());
var add_button = document.getElementById('add');
var add_form = document.getElementById('add_form');
var target = document.getElementById('target');
var forms = document.getElementById('forms');
var status_request = document.getElementById("status_request");
var count_input = 0;
function getValueAndConvertToNumber() {
    var vector = [];
    for (var _i = 0, _a = Array.prototype.slice.call(target.getElementsByTagName("input")); _i < _a.length; _i++) {
        var item = _a[_i];
        vector.push(Number(item.value));
    }
    return vector;
}
function addElement() {
    for (var _i = 0, _a = Array.prototype.slice.call(forms.getElementsByTagName("form")); _i < _a.length; _i++) {
        var item = _a[_i];
        item.insertAdjacentHTML('beforeend', "<input id=" + item.getElementsByTagName('input').length + ">");
    }
    count_input += 1;
}
function addElementForm() {
    var value_form = "array" + forms.getElementsByTagName('form').length + "[]";
    forms.insertAdjacentHTML('beforeend', "<form name=" + value_form + " id=\"" + value_form + "\">");
    var get_new_form = document.getElementById("" + value_form);
    for (var i = 1; i <= count_input; i++) {
        get_new_form.insertAdjacentHTML('beforeend', "<input id=" + i + ">");
    }
}
